export default {
	name:"Nj_header",
	data() {
		return {
			TabCur: 1,
			scrollLeft: 0,
			id:"",
			list:[]
		};
	},
	

methods:{
	tabSelect(id) {
	this.TabCur =id;
    this.$parent.requeslist(this.TabCur);

	},	
	requests(){
		let that=this;
		uni.request({
			url:"http://localhost:58793/Api/Index/Header",
			method:"GET",
			success:function(res){
			
				that.list=res.data;
			}
		})
	}
},
mounted(){
	this.requests();
	  this.$parent.requeslist(this.TabCur);
}	
}
